
import Vue, { PropType } from "vue"

// Types
import { Toc } from "~/src/types/Post"

export default Vue.extend({
  props: {
    toc: {
      type: Array as PropType<Toc[]>,
      required: true,
      default: () => [],
    },
  },
  data() {
    return {
      tocToggled: false,
    }
  },
})
