
import Vue from "vue"

/* Interfaces */
import type { FetchReturn } from "@nuxt/content/types/query-builder"

export default Vue.extend({
  props: {
    currentSlug: {
      type: String,
      required: true,
      default: null,
    },
  },
  data() {
    return {
      prev: {} as FetchReturn,
      next: {} as FetchReturn,
    }
  },
  async fetch() {
    const [prev, next] = (await this.$content("blog")
      .only(["title", "slug"])
      .sortBy("createdAt", "asc")
      .surround(this.currentSlug)
      .fetch()) as FetchReturn[]

    this.prev = prev
    this.next = next
  },
})
