
import Vue from "vue"

export default Vue.extend({
  props: {
    brand: {
      type: String,
      required: true,
      default: "",
    },
  },
  methods: {
    /**
     * Checks if passed value matches with the prop value.
     * @prop {string} value
     * @returns {boolean}
     */
    isSame(value: string): boolean {
      return this.brand?.toLowerCase() === value?.toLowerCase()
    },
  },
})
